:root {
  --color-graphite: #292D3A;
  --color-white: #ffffff;
  --color-transparent: rgba(255, 255, 255, 0);
  --color-orange: #F26531;
  --color-green: #007934;
  --color-pale-yellow: #FDF8E7;
  --color-floral-white: #FEFBF3;
  --color-divider: #EAEAEB;
  --gradient-service-card: linear-gradient(228.59deg, rgba(242, 101, 49, 0.4) 11.24%, rgba(0, 121, 52, 0.4) 91.81%), linear-gradient(0deg, rgba(41, 45, 58, 0.5), rgba(41, 45, 58, 0.5));
  --default-transition: 0.3s ease;
}

@font-face {
  font-family: Inter;
  font-display: swap;
  src: url("../fonts/inter-regular.woff2") format("woff2"), url("../fonts/inter-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Inter;
  font-display: swap;
  src: url("../fonts/inter-bold.woff2") format("woff2"), url("../fonts/inter-bold.woff") format("woff");
  font-weight: 700;
  font-style: bold;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 62.5%;
  line-height: 24px;
  font-family: "Inter", "Arial", sans-serif;
  color: var(--color-graphite);
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  width: 100%;
  height: 100%;
  background-color: var(--color-white);
  font-size: 1.4rem;
}

a {
  color: var(--color-graphite);
  text-decoration: none;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 100rem var(--color-white);
  -webkit-text-fill-color: var(--color-graphite);
}

input,
textarea {
  border-radius: 0;
}

input::placeholder,
textarea::placeholder {
  opacity: 1;
}

input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear,
input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  background-color: var(--color-transparent);
  border: none;
}

h1 {
  margin-bottom: 4.4rem;
  margin-top: 0;
  font-size: 3rem;
  line-height: 1;
}

h2 {
  margin: 0;
}

p {
  margin: 0;
}

section + section {
  margin-top: 4.8rem;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
}

.wrapper main {
  flex-grow: 1;
  padding-top: 4rem;
  padding-bottom: 4.8rem;
}

.mobile-show-inline {
  display: none;
}

.container {
  width: 100%;
  max-width: 102.4rem;
  margin: 0 auto;
  padding: 0 4.8rem;
}

.services {
  overflow: visible;
}

.services__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.6rem;
  height: 100%;
}

.services__item {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 28rem;
  border-radius: 0.4rem;
  transition: opacity var(--default-transition);
  overflow: hidden;
}

.services__item:hover {
  opacity: 0.8;
}

.services__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  top: 0;
  left: 0;
}

.services__link {
  background: var(--gradient-service-card);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 3.2rem 4rem 4rem 4rem;
  color: var(--color-white);
}

.services__title {
  margin-bottom: auto;
  line-height: 2.8rem;
}

.services__button {
  align-self: flex-start;
  margin-top: 2.4rem;
}

.other-services {
  max-width: 67rem;
}

.other-services {
  display: inline-block;
  flex-wrap: wrap;
}

.other-services__link {
  position: relative;
  line-height: 3rem;
  font-size: 1.6rem;
  letter-spacing: 0%;
  transition: var(--default-transition);
}

.other-services__link:hover {
  opacity: 0.7;
}

.other-services__link:not(:last-of-type) {
  margin-right: 2.5rem;
}

.other-services__link:not(:first-child)::before {
  content: "";
  position: absolute;
  top: 0.8rem;
  width: 0.5rem;
  height: 0.5rem;
  left: -1.5rem;
  border-radius: 50%;
  background-color: var(--color-divider);
}

.other-services__link span {
  color: var(--color-orange);
}

.other-services-conatiner .link-green {
  display: inline-block;
  margin-top: 1.6rem;
}

.features {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  border-radius: 0.4rem;
  overflow: hidden;
}

.features__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  min-height: 26.4rem;
  padding: 2.8rem 2.2rem 4rem 2.2rem;
  background-color: var(--color-pale-yellow);
  transition: var(--default-transition);
}

.features__item:hover {
  opacity: 0.8;
}

.features__item:nth-child(even) {
  background-color: var(--color-floral-white);
}

.features__img {
  width: 8rem;
  height: 8rem;
}

.features__text {
  text-align: center;
}

.features__text-green {
  color: var(--color-green);
}

.button {
  display: inline-flex;
  padding: 0.4rem 1.7rem;
  font-size: 1.3rem;
  line-height: 2.4rem;
  background-color: var(--color-orange);
  border-radius: 4rem;
  white-space: nowrap;
}

.pale-grey-title {
  margin-bottom: 1.6rem;
  font-weight: 700;
  font-size: 1.1rem;
  opacity: 0.3;
  line-height: 1;
  text-transform: uppercase;
}

.link-green {
  font-size: 1.6rem;
  color: var(--color-green);
  font-weight: 700;
  transition: var(--default-transition);
  line-height: 1;
}

.link-green:hover {
  opacity: 0.8;
}

@media (max-width: 1024px) {
  section + section {
    margin-top: 3.2rem;
  }

  .services__item {
    min-height: 24rem;
  }

  .services__text {
    font-size: 1.2rem;
    line-height: 2rem;
  }

  .services__link {
    padding: 2.8rem 3.2rem 3.2rem 3.2rem;
  }

  .services__title {
    font-size: 1.5rem;
    line-height: 2rem;
  }

  .services__button {
    margin-top: 2rem;
  }
}

@media (max-width: 768px) {
  h1 {
    margin-bottom: 2.8rem;
    font-size: 2rem;
    line-height: 2.4rem;
  }

  .wrapper main {
    padding-top: 2.4rem;
    padding-bottom: 3.2rem;
  }

  .mobile-show-inline {
    display: inline;
  }

  .mobile-hide {
    display: none;
  }

  .container {
    padding: 0 3.2rem;
  }

  .services__list {
    gap: 0;
    overflow: visible;
  }

  .services__item {
    width: 41vw;
  }

  .other-services__inner,
  .other-services {
    display: flex;
    flex-direction: column;
  }

  .other-services__inner {
    flex-direction: column-reverse;
  }

  .other-services__link {
    font-size: 1.3rem;
    line-height: 2.8rem;
  }

  .other-services__link:not(:last-of-type) {
    margin-right: 0;
  }

  .other-services__link:not(:first-child)::before {
    content: none;
  }

  .other-services-conatiner .link-green {
    margin-top: 2rem;
  }

  .features {
    grid-template-columns: 1fr;
  }

  .features__item {
    flex-direction: row;
    gap: 1.2rem;
    min-height: 10.4rem;
    padding: 1.6rem 1.2rem;
  }

  .features__img {
    width: 7.2rem;
    height: 7.2rem;
  }

  .features__text {
    text-align: left;
    font-size: 1.2rem;
    line-height: 2rem;
  }

  .button {
    font-size: 1.2rem;
    padding: 0.3rem 1.6rem;
  }

  .pale-grey-title {
    margin-bottom: 2rem;
    font-size: 1rem;
  }

  .link-green {
    font-size: 1.3rem;
  }
}

@media (max-width: 575px) {
  .services__item {
    width: 67.5vw;
  }
}